import * as React from 'react'

const TodoForm = ({list, setList, setPrevent}) => {
    const [item, setItem] = React.useState('')
    const val = item.trim();
    const duplicated = list?.filter((item) => item?.item.trim() !== val)
    const value = list.filter((item) => item?.item.trim() === val)
    const handleSubmit = (e) => {
        e.preventDefault()
        const newItem = item.trim()
        if (!newItem) {
            setItem(newItem)
            return alert('Please enter an item')
        }
        console.log(duplicated)
        if(value.length> 0){
            alert('This item already exist')
            setList([...duplicated])
        }
        setItem('')
        return setList(prev => [{ id: (new Date()).getTime(), item: newItem, completed: false }, ...prev])
    }
    return (
        <form onSubmit={handleSubmit} autoComplete='off'>
            <input value={item} type='text' onChange={(e) => setItem(e.target.value)} placeholder='Enter an item .......' />
            <button type='submit'>Save</button>
        </form>
    )
}
export default TodoForm